module.exports = [[module.id, "/*\n * CSS Styles that are needed by jScrollPane for it to operate correctly.\n *\n * Include this stylesheet in your site or copy and paste the styles below into your stylesheet - jScrollPane\n * may not operate correctly without them.\n */\n\n.jspContainer\n{\n\toverflow: hidden;\n\tposition: relative;\n}\n\n.jspPane\n{\n\tposition: absolute;\n}\n\n.jspVerticalBar\n{\n\tposition: absolute;\n\ttop: 0;\n\tright: 0;\n\twidth: 16px;\n\theight: 100%;\n\tbackground: red;\n}\n\n.jspHorizontalBar\n{\n\tposition: absolute;\n\tbottom: 0;\n\tleft: 0;\n\twidth: 100%;\n\theight: 16px;\n\tbackground: red;\n}\n\n.jspCap\n{\n\tdisplay: none;\n}\n\n.jspHorizontalBar .jspCap\n{\n\tfloat: left;\n}\n\n.jspTrack\n{\n\tbackground: #dde;\n\tposition: relative;\n}\n\n.jspDrag\n{\n\tbackground: #bbd;\n\tposition: relative;\n\ttop: 0;\n\tleft: 0;\n\tcursor: pointer;\n}\n\n.jspHorizontalBar .jspTrack,\n.jspHorizontalBar .jspDrag\n{\n\tfloat: left;\n\theight: 100%;\n}\n\n.jspArrow\n{\n\tbackground: #50506d;\n\ttext-indent: -20000px;\n\tdisplay: block;\n\tcursor: pointer;\n\tpadding: 0;\n\tmargin: 0;\n}\n\n.jspArrow.jspDisabled\n{\n\tcursor: default;\n\tbackground: #80808d;\n}\n\n.jspVerticalBar .jspArrow\n{\n\theight: 16px;\n}\n\n.jspHorizontalBar .jspArrow\n{\n\twidth: 16px;\n\tfloat: left;\n\theight: 100%;\n}\n\n.jspVerticalBar .jspArrow:focus\n{\n\toutline: none;\n}\n\n.jspCorner\n{\n\tbackground: #eeeef4;\n\tfloat: left;\n\theight: 100%;\n}\n\n/* Yuk! CSS Hack for IE6 3 pixel bug :( */\n\n* html .jspCorner\n{\n\tmargin: 0 -3px 0 0;\n}\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]